<!--suppress ALL -->
<template>
    <div>
        <mt-header title="RCSA报告" :fixed="true">
            <router-link to="/" slot="left" >
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_back@2x.png">
                </mt-button>
            </router-link>
            <router-link to="/" slot="right">
                <mt-button>
                    <img class="header-img" src="static/image/ic_navbar_home@2x.png">
                </mt-button>
            </router-link>
        </mt-header>
        <div class="has-footer">
        <div class="ban_top"></div>
        <div class="pic">
            <!-- four button-->
            <div class="item">
                <mt-button class="item_btn" v-bind:class="{true:'clickColor',false:''}[backColor=='business']"  @click.native.prevent="active='tab-container-1'">总体分析
                </mt-button>
                <mt-button class="item_btn" v-bind:class="{true:'clickColor',false:''}[backColor=='finance']"  @click.native.prevent="active='tab-container-2'">固有风险
                </mt-button>
                <mt-button class="item_btn" v-bind:class="{true:'clickColor',false:''}[backColor=='worth']"  @click.native.prevent="active='tab-container-3'">有效性
                </mt-button>
                <mt-button class="item_btn" v-bind:class="{true:'clickColor',false:''}[backColor=='inverstment']"  @click.native.prevent="active='tab-container-4'">剩余风险
                </mt-button>
                <mt-button class="item_btn" v-bind:class="{true:'clickColor',false:''}[backColor=='rectification']"  @click.native.prevent="active='tab-container-5'">整改情况
                </mt-button>
            </div>
            <!-- tab-container -->
            <mt-tab-container class="" v-model="active" swipeable>
                <mt-tab-container-item id="tab-container-1">
                    <!--本期评估派工计划-->

                    <div class="part-title">
                        <span class="part_title">本期评估派工计划</span>
                    </div>
                    <div style="margin-top: 20px">
                        <div class="table-scroll"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>

