                                <span class="table-title-type" style="width: 3rem">派工层级</span>
                                <span class="table-title-type"style="width: 3rem">派工时间</span>
                                <span class="table-title-type"style="width: 3rem">自评估派发率</span>
                                <span class="table-title-type">
                                <span class="titletop">本期派发风险点个数</span>
                                 <span class="titlebottom">
                                     <span class="titleboleft">总公司</span>
                                     <span class="titleboright">分公司</span>
                                 </span>
                                </span>
                                <span class="table-title-type">
