                                     <span class="titlebottom">
                                         <span class="titleboleft">总公司</span>
                                         <span class="titleboright">分公司</span>
                                     </span>
                                 </span>
                            </div>
                            <div class="table-content-line2 even-odd" v-for="item in dispatchingPlan">
                                <span class="table-content-value"style="width: 3rem">{{item.companyLevel}}</span>
                                <span class="table-content-value"style="width: 3rem">{{item.date }}</span>
                                <span class="table-content-value"style="width: 3rem">{{item.dispatchingPercent}}</span>
                                <span class="table-content-value1">{{item.headRiskNum}}</span>
                                <span class="table-content-value1">{{item.branchRiskNum}}</span>
                                <span class="table-content-value1">{{item.headControlNum}}</span>
                                <span class="table-content-value1">{{item.branchControlNum}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="part-title">
                        <span class="part_title">本期评估总体结果分析</span>
                    </div>
                    <div>
