                                <span class="table-title-type1" style="width:2rem">编号</span>
                                <span class="table-title-type1">风险点</span>
                                <span class="table-title-type1">流程归属层级</span>
                                <span class="table-title-type1" style="">损失事件涉及损失金额</span>
                                <span class="table-title-type1">损失事件个数</span>
                                <span class="table-title-type1">损失事件<br>涉及非财务损失等级</span>
                                <span class="table-title-type1" style="">主责部门</span>
                                <span class="table-title-type1">固有风险等级</span>
                                <span class="table-title-type1">剩余风险等级</span>
                                <span class="table-title-type1">综合有效性</span>
                            </div>
                            <div class="table-content-line1 even-odd" v-for="(item, index) in currentEvaluteResult">
