                                <span class="table-content-value2">{{item.riskPoint}}</span>
                                <span class="table-content-value2">{{item.companyLevel }}</span>
                                <span class="table-content-value2">{{item.lossAmount}}</span>
                                <span class="table-content-value2">{{item.eventNum}}</span>
                                <span class="table-content-value2">{{item.nonFinancialLevel}}</span>
                                <span class="table-content-value2">{{item.responsibleDepartment}}</span>
                                <span class="table-content-value2">{{item.inherentRiskLevel}}</span>
                                <span class="table-content-value2">{{item.residualRisk}}</span>
                                <span class="table-content-value2">{{item.comprehensiveValid}}</span>
                            </div>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-container-2">
                    <!--截止到统计时间-->
                    <div class="part-title">
                        <span class="part_title">固有风险评估</span>
                    </div>
                    <div style="margin-top: 20px">
                        <div class="table-fixed">
                            <span class="table-title-name">指标</span>
                            <span class="table-content-name even-odd">5(几乎肯定)</span>
                            <span class="table-content-name even-odd">4(很可能)</span>
                            <span class="table-content-name even-odd">3(可能)</span>
                            <span class="table-content-name even-odd">2(不太可能)</span>
                            <span class="table-content-name even-odd">1(罕见)</span>

                        </div>
                        <div class="table-scroll3"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>
