                                <span class="table-title-type3" >1(几乎无影响)</span>
                                <span class="table-title-type3">2(轻微)</span>
                                <span class="table-title-type3">3(一般)</span>
                                <span class="table-title-type3">4(严重)</span>
                                <span class="table-title-type3">5(灾难性)</span>
                            </div>
                            <div class="table-content-line3 even-odd" v-for="item in inherentRiskS">
                                <span class="table-content-value3">{{item.level1}}</span>
                                <span class="table-content-value3">{{item.level2 }}</span>
                                <span class="table-content-value3">{{item.level3}}</span>
                                <span class="table-content-value3">{{item.level4}}</span>
                                <span class="table-content-value3">{{item.level5}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="part-title">
                        <span class="part_title">本期固有风险等级情况统计</span>
                    </div>
                    <div id="pic1" class="qushitu"></div>
                    <div class="part-title">
                        <span class="part_title">本期固有风险等级占比情况统计</span>
                    </div>
                    <div id="pic2" style="width:6rem;height:6.36rem;margin-left:1rem"></div>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-container-3">
