                                <span class="table-content-value4">{{item.organization}}</span>
                                <span class="table-content-value4">{{item.controlEffictive}}</span>
                                <span class="table-content-value4">{{item.responsibleDepartment}}</span>
                            </div>
                        </div>
                    </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-container-4">
