                        <span class="part_title">本期剩余风险等级情况统计</span>
                    </div>
                    <div id="pic4" style="height: 6.5rem;width:8rem;margin-left: 0rem"></div>
                    <div class="fenge_two"></div>
                    <div class="part-title">
                        <span class="part_title">本期剩余风险等级结果趋势对比</span>
                    </div>
                    <div style="margin-top: 20px">
