                            <span class="table-title-name">剩余风险等级</span>
                            <span class="table-content-name even-odd">1级-低风险</span>
                            <span class="table-content-name even-odd">2级-中等风险	</span>
                            <span class="table-content-name even-odd">3级-高风险</span>
                            <span class="table-content-name even-odd">4级-极高风险</span>


                        </div>
                        <div class="table-scroll3"
                             @touchstart.stop
                             @mousedown.stop
                             @touchmove.stop
                             @mousemove.stop
                             @mouseleave.stop
                             @touchend.stop>
                            <div class="table-content-line3">
                                <span class="table-title-type3" >本期数据（%）</span>
                                <span class="table-title-type3">往期数据（%）</span>
                                <span class="table-title-type3">同比变化</span>
                                <span class="table-title-type3">趋势</span>
                            </div>
                            <div class="table-content-line3 even-odd" v-for="item in currentResidualRiskLevel">
                                <span class="table-content-value3">{{item.currentData}}</span>
                                <span class="table-content-value3">{{item.lastData }}</span>
                                <span class="table-content-value3">{{item.change}}</span>
                                <span class="table-content-value3">{{item.trend}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="part-title">
                        <span class="part_title">本期剩余风险等级分布情况(按一级流程)</span>
                    </div>
                    <div class="table-scroll5"
                         @touchstart.stop
                         @mousedown.stop
                         @touchmove.stop
                         @mousemove.stop
                         @mouseleave.stop
                         @touchend.stop>
                        <div class="table-content-line5">
                            <span class="table-title-type5"style="width:2rem">序号	</span>
                            <span class="table-title-type5">一级流程</span>
                            <span class="table-title-type5">流程归属层级</span>
                            <span class="table-title-type5">风险点个数</span>
                            <span class="table-title-type5" style="width: 8rem">
                                <span class="titletop">剩余风险等级</span>
                                 <span class="titlebottom">
                                     <span class="titleboleft">极高风险</span>
                                     <span class="titleboright">高风险</span>
                                      <span class="titleboleft">中等风险</span>
                                     <span class="titleboright">低风险</span>
                                 </span>
                            </span>
                        </div>
                        <div class="table-content-line5 even-odd" v-for="(item, index) in residualRiskDistribute">
                            <span class="table-content-value5"style="width:2rem">{{index+1}}</span>
                            <span class="table-content-value5">{{item.level1Process}}</span>
                            <span class="table-content-value5">{{item.companyLevel}}</span>
                            <span class="table-content-value5">{{item.riskNum }}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.highestRisk}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.highRisk}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.mediumRisk}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.lowerRisk}}</span>
                        </div>
                    </div>
                    <div class="part-title">
                        <span class="part_title">本期剩余风险等级分布情况(按业务条线)</span>
                    </div>
                    <div class="table-scroll5"
                         @touchstart.stop
                         @mousedown.stop
                         @touchmove.stop
                         @mousemove.stop
                         @mouseleave.stop
                         @touchend.stop>
                        <div class="table-content-line5">
                            <span class="table-title-type5"style="width:2rem">序号	</span>
                            <span class="table-title-type5">业务条线</span>
                            <span class="table-title-type5">流程归属层级</span>
                            <span class="table-title-type5">风险点个数</span>
                            <span class="table-title-type5" style="width: 8rem">
                                <span class="titletop">剩余风险等级</span>
                                 <span class="titlebottom">
                                     <span class="titleboleft">极高风险</span>
                                     <span class="titleboright">高风险</span>
                                      <span class="titleboleft">中等风险</span>
                                     <span class="titleboright">低风险</span>
                                 </span>
                            </span>
                        </div>
                        <div class="table-content-line5 even-odd" v-for="(item, index) in residualRiskDistribute">
                            <span class="table-content-value5"style="width:2rem">{{index+1}}</span>
                            <span class="table-content-value5">{{item.level1Process}}</span>
                            <span class="table-content-value5">{{item.companyLevel}}</span>
                            <span class="table-content-value5">{{item.riskNum }}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.highestRisk}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.highRisk}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.mediumRisk}}</span>
                            <span class="table-content-value5"style="width: 2rem;">{{item.lowerRisk}}</span>
                        </div>
                    </div>
                    <!--<div id="pic8" style="height: 6.5rem"></div>-->
                    <!--<div class="fenge_two"></div>-->
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-container-5">
                    <div class="part-title">
                        <span class="part_title">本期风险点应对状态统计</span>
                    </div>
                    <div id="pic5" style="height: 6.5rem;width:6rem;margin-left: 1rem"></div>
                    <div class="fenge_two"></div>
                    <div class="part-title">
                        <span class="part_title">本期风险点应对情况统计</span>
                    </div>
                    <div class="table-scroll6"
                         @touchstart.stop
                         @mousedown.stop
                         @touchmove.stop
                         @mousemove.stop
                         @mouseleave.stop
                         @touchend.stop>
                        <div class="table-content-line6">
                            <span class="table-title-type6">截止目前<br>本期自评估完成率	</span>
                            <span class="table-title-type6">风险点测试<br>合格个数</span>
                            <span class="table-title-type6" style="width:6rem;">
                                <span class="titletop1" >本期风险点测试存在缺陷情况下</span>
                                 <span class="titlebottom1">
                                     <span class="titleboleft1">新增整改措施个数</span>
                                     <span class="titleboright1">未增加整改措施个数</span>
                                 </span>
                            </span>
                            <span class="table-title-type6">截止目前<br>本期整改完成率</span>
                        </div>
                        <div class="table-content-line6 even-odd" v-for="item in riskPointResponse">
                            <span class="table-content-value6">{{item.assessmentCompletionRate}}</span>
                            <span class="table-content-value6">{{item.qualifyNum}}</span>
                            <span class="table-content-value6"style="width: 3rem;">{{item.newMeasures }}</span>
                            <span class="table-content-value6"style="width: 3rem;">{{item.noMeasures}}</span>
                            <span class="table-content-value6">{{item.rectificationCompleteRate}}</span>
                        </div>
                    </div>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
        </div>
        <div class="footer">
                <div class="footer1">
                    <span @click="showCompany" class="company-show">{{currentCompany.codeName}}</span>
                    <span @click="showCompany" class="company-show">{{currentCompany.codeName}}</span>

                    <span @click="showYear" class="company-show">{{currentYear}}</span>
                </div>
                <div class="footer2">
                    <span @click="showQuarter" class="company-show">{{currentQuarter}}</span>
                    <span @click="" class="company-show">查询</span>
                </div>
        </div>
        <mt-popup
                v-model="popupVisible"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancelCompany">取消</span>
                <span>选择机构</span>
                <span class="confirm" @click="confirmCompany">确认</span>
            </div>
            <div class="select-company">
                <button v-for="company in companys" @click="btnSelect(company)" :class="[company==tempCompany?'selected':'']">{{company}}</button>
                <div class="blank-fill"></div>
            </div>

        </mt-popup>
        <mt-popup
                v-model="popupQuarterVisible"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancelQuarter">取消</span>
                <span>选择季度</span>
                <span class="confirm" @click="confirmQuarter">确认</span>
            </div>
            <div class="select-company">
                <button v-for="item in quarters" @click="btnQuarterSelect(item)":class="[item==tempQuarter?'selected':'']">{{item}}</button>
                <div class="blank-fill"></div>
            </div>

        </mt-popup>
        <mt-popup
                v-model="popupYearVisible"
                class="popup"
                position="bottom">
            <div class="select-title">
                <span class="cancel" @click="cancelYear">取消</span>
                <span>选择年份</span>
                <span class="confirm" @click="confirmYear">确认</span>
            </div>
            <div class="select-company">
                <button v-for="item in Years" @click="btnYearSelect(item)":class="[item==tempYear?'selected':'']">{{item}}</button>
                <div class="blank-fill"></div>
            </div>

        </mt-popup>

    </div>
</template>

<script>
    export default {
        data(){
            return {
                dispatchingPlan:[
                    {
                        companyLevel:'总公司',
                        date:'2018/3/1',
                        dispatchingPercent:'1.80%',
                        headRiskNum:'10',
                        branchRiskNum:'2',
                        headControlNum:'11',
                        branchControlNum:'2'
                    }
                ],
                currentEvaluteResult:[
                    {
                        riskPoint:'参选供应商',
                        companyLevel:'总公司',
                        lossAmount:'100',
                        eventNum:'11',
                        nonFinancialLevel:'一般',
                        responsibleDepartment:'风险管理部门',
                        inherentRiskLevel:'高风险',
                        residualRisk:'高风险',
                        comprehensiveValid:'重大缺陷'
                    }
                ],
                inherentRiskS:[
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    },
                    {
                        'level1': "2",
                        'level2': "1",
                        'level3': "1",
                        'level4': "1",
                        'level5': "1"
                    }
                ],
                inherentRiskPercent:[
                    {value:335, name:'极高风险'},
                    {value:310, name:'高风险'},
                    {value:234, name:'中等风险'},
                    {value:135, name:'低风险'},
                ],
                regval:/\d+/,
                pieColor :[
                    {
                        start:'#7B88FF',
                        end:'#204BEF'
                    },{
                        start:'#1880FE',
                        end:'#002778'
                    },{
                        start:'#0098D0',
                        end:'#005FA3'
                    },{
                        start:'#86EACD',
                        end:'#3FC391'
                    },{
                        start:'#3CA9F1',
                        end:'#1188D7'
                    },{
                        start:'#69E4FA',
                        end:'#38C5F3'
                    }
                ],
                effictiveResult:[
                    {controlPoint:'项目存在开发商企业资',
                      organization:'总公司' ,
                       controlEffictive:'重大缺陷',
                        responsibleDepartment:'风险管理部'
                    },
                    {controlPoint:'数据错误或不合规',
                        organization:'总公司' ,
                        controlEffictive:'实质性漏洞',
                        responsibleDepartment:'风险管理部'
                    }
                ],
                currentResidualRiskLevel:[
                    {
                        currentData: '60%',
                        lastData: '50',
                        change: "10%",
                        trend: '上升'
                    },
                    {
                        currentData:'60%',
                        lastData:'50',
                        change:"10%",
                        trend:'上升'
                    },
                    {
                        currentData:'60%',
                        lastData:'50',
                        change:"10%",
                        trend:'上升'
                    },
                    {
                        currentData:'60%',
                        lastData:'50',
                        change:"10%",
                        trend:'上升'
