        justify-content: center;
        align-items: center;
        box-sizing: border-box;
    }
    /*公司年份等选择样式*/
    .company-show{
        display: inline-block;
        border: 1px solid #116DCF;
        border-radius: 0.3rem;
        width: 1.5rem;
        height: 0.5rem;
        line-height: 0.5rem;
        text-align: center;
        color: #116DCF;
        margin: 0.1rem 0 0.1rem 0.00rem;
    }
    .company-show:after{
        content: " ";
        display: inline-block;
        line-height: 0.113rem;
        height: 0.113rem;
        width: 0.113rem;
        border-bottom: 1px solid #116DCF;
        border-right: 1px solid #116dcf;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        top: -0.07rem;
        margin-left: 0.14rem;
    }
    .select-company{
        text-align: center;
        margin-top: 1rem;
        height:100%;
        overflow-y:scroll;
        -webkit-overflow-scrolling: touch;
    }
    .select-company>button{
        display: inline-block;
        background: transparent;
        float: left;
        height: 0.6rem;
        line-height: 0.6rem;
        width: 1.4rem;
        border-radius: 1rem;
        margin:0.05rem 0;
        border: 0.01rem solid transparent;
        box-sizing: border-box;
    }
    .select-company>button.selected{
        color: #116dfc;
        border: 0.01rem solid #116dfc
    }
    .select-company>button:nth-child(even){
        margin-right: 1.26rem;
        margin-left: 0.62rem;
    }
    .select-company>button:first-child{
        margin-right: 5.48rem;
        margin-left: 0.62rem;
        margin-top: 0.26rem;
    }
    .select-company>button:nth-child(2n+3){
        margin-right: 1.56rem;
        margin-left: 1.26rem;
    }
    .select-title{
        position: absolute;
        top: 0;
        left: 0;
        height: 1rem;
        right: 0;
        margin: 0 0.3rem;
        border-bottom: 1px solid #ECECEC;
        line-height: 1rem;
        text-align: center;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0;
        box-sizing: border-box;
        /* background: #fff;
         z-index: 1;*/
    }
    .select-title>.cancel{
        opacity: 0.7;
        font-size: 0.28rem;
        color: #3B4053;
        float: left;
    }
    .select-title>.confirm{
        opacity: 0.7;
        font-size: 0.28rem;
        color: #116DCF;
        float: right;
    }
    .blank-fill{
        display: inline-block;
        width: 100%;
        height: 1rem;
    }
    .footer{
        right: 0;
        bottom: 0;
        left: 0;
        position: fixed;
        z-index: 1;
        background-image: linear-gradient(180deg, #d9d9d9, #d9d9d9 50%, transparent 50%);
        background-size: 100% 1px;
        background-repeat: no-repeat;
        background-position: top left;
        background-color: #fafafa;
    }
    .footer1,.footer2{
        display: flex;
        /*right: 0;*/
        /*bottom: 0;*/
        /*left: 0;*/
        /*position: absolute;*/
        text-align: center;
        justify-content: space-evenly;
    }
    .has-footer{
        margin-bottom: 2.8rem;
    }
</style>



