                        end: '#5599FF'
                    }, {
                        start: '#77DDFF',
                        end: '#69E4FA'
                    },{
                        start: '#3CA9F1',
                        end: '#1188D7'
                    }, {
                        start: '#69E4FA',
                        end: '#38C5F3'
                    }, {
                        start: '#5599FF',
                        end: '#33CCFF'
                    }, {
                        start: '#77DDFF',
                        end: '#86EACD'
                    }, {
                        start: '#33CCFF',
                        end: '#77DDFF'
                    }, {
                        start: '#77DDFF',
                        end: '#69E4FA'
                    }, {
                        start: '#3FEE91',
                        end: '#86EACD'
                    }, {
                        start: '#69E4FA',
                        end: '#38C5F3'
                    },  {
                        start: '#69E4FA',
                        end: '#38C5F3'
                    }
                ],
                allData:{
                    initData: {},           //风险成因分析数据
                    eventsData:{},          //事件分析类型
                    financeData:{},         //财务损失类型
                    nonFinanceData:{},      //非财务影响
                },
                slots: [
                    {
                        flex: 1,
                        values: [],
                        className: 'slot1',
                        textAlign: 'right'
                    }, {
                        divider: true,
                        content: '-',
                        className: 'slot2'
                    }, {
                        flex: 1,
                        values: ["第一季度", "第二季度", "第三季度", "第四季度"],
                        className: 'slot4',
                        textAlign: 'left'
                    }
                ]

            }
        },
        watch: {
            active: function (val, oldVal) {
                if (val == 'tab-container-1') {
                    this.drawPie(this.allData.initData.operationRise,'operaterisepie');
                } else if (val == 'tab-container-2') {
                    this.drawPie(this.allData.eventsData.operationRise,'operaterisepie2');
                } else if (val == 'tab-container-3') {
                    this.drawPie(this.allData.financeData.lossEvents,'losseventspie3');
                    this.drawPie(this.allData.financeData.lossMoney,'lossmoneypie3');
                } else if (val == 'tab-container-4') {
                    this.drawPie(this.allData.nonFinanceData.levelDistribution,'leveldistribution');
                    this.drawPie(this.allData.nonFinanceData.eventsProportion,'eventsproportion');
                }
            },
            tabtype: function (val, oldVal) {
                if (val == 'tab-pie-1') {
                    this.drawPie(this.allData.initData.operationRise,'operaterisepie');
                } else if (val == 'tab-pie-2') {
                    this.drawPie(this.allData.initData.nonLossEvents,'nonlosseventspie');
                } else if (val == 'tab-pie-3') {
                    this.drawPie(this.allData.initData.lossEvents,'losseventspie')
                } else if (val == 'tab-pie-4') {
                    this.drawPie(this.allData.initData.lossMoney,'lossmoneypie')
                }

            },
            tabtype2: function (val, oldVal) {
                if (val == 'tab2-pie-1') {
                    this.drawPie(this.allData.eventsData.operationRise,'operaterisepie2');
                } else if (val == 'tab2-pie-2') {
                    this.drawPie(this.allData.eventsData.nonLossEvents,'nonlosseventspie2');
                } else if (val == 'tab2-pie-3') {
                    this.drawPie(this.allData.eventsData.lossEvents,'losseventspie2')
                } else if (val == 'tab2-pie-4') {
                    this.drawPie(this.allData.eventsData.lossMoney,'lossmoneypie2')
                }

            },
        },
        created() {
            var date = new Date;
            this.currentYear = date.getFullYear();
            this.currentQuarter = Math.ceil((date.getMonth() + 1) / 3);
            this.drawRiseReason();
        },
        methods: {
            drawRiseReason() {
                axios.get('../static/json/PointClassify.json').then(res => {
                    this.allData = res.data.allData;
                    this.drawPie(this.allData.initData.operationRise,'operaterisepie');
                    this.drawPie(this.allData.eventsData.nonLossEvents,'nonlosseventspie2');
                    this.drawPie(this.allData.eventsData.lossEvents,'losseventspie2');
                    this.drawPie(this.allData.eventsData.lossMoney,'lossmoneypie2');
                })
            },
            drawPie(data,element){
                var assistData = [];
                var seriesData =this.getNewList(data,assistData);
                var option={
                    background: "#FFFFFF",
                    tooltip: {
                        show: true,
                        backgroundColor: '#FFFFFF',
                        boxShadow: '0 2px 40px 0 rgba(59,64,83,0.30)',
                        borderRadius: 4 * this.relativeRate,
                        textStyle: {
                            color: '#3B4053',
                            fontSize: 20 * this.relativeRate,
                        },
                        formatter: '{b}<br/>{c}%',
                        extraCssText: 'box-shadow: 0 2px 40px 0 rgba(59,64,83,0.30)'
                    },
                    series: [{
                        type: "pie",
                        name: "",
                        legendHoverLink: true,
                        hoverAnimation: true,
                        hoverOffset:3,
                        minAngle: 20,
                        z: 10,
                        center: ['50%', '50%'],
                        radius: [110 * this.relativeRate, 240 * this.relativeRate],
                        itemStyle: {
                            normal: {
                                borderWidth: 10.5 * this.relativeRate,
                                borderColor: '#FFF',
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                position: "inside",
                                formatter: x => {
                                    var resultName = x.name;
                                    var reg = this.regval.exec(resultName);
                                    if (reg) {
                                        resultName = resultName.replace(reg[0], "");
                                        if (resultName.length > 3) {
                                            resultName = resultName.split("");
                                            resultName.splice(3, 0, reg[0] + "\n");
                                            resultName = resultName.join("");
                                            return resultName
                                        } else {
                                            resultName = resultName.split("");
                                            resultName.splice(3, 0, "\n" + reg[0]);
                                            resultName = resultName.join("");
                                            return resultName
                                        }

                                    }
                                    if (resultName.length > 4) {
                                        resultName = resultName.split("");
                                        resultName.splice(4, 0, "\n");
                                        resultName = resultName.join("");
                                    }
                                    return resultName;
                                },
                                color: '#fff',
                                fontSize: 24 * this.relativeRate,
                                rotate: 90
                            }
                        },
                        data: seriesData
                    }, {
                        type: "pie",
                        name: "",
                        z: 1,
                        minAngle: 20,
                        silent: true,
                        radius: [0 * this.relativeRate, 110 * this.relativeRate],
                        itemStyle: {
                            normal: {
                                color: 'rgba(255,255,255,0)',
                                borderColor: 'rgba(59, 64, 83, 0.1)',
                                borderType: 'solid'
                            }
                        },
                        label: {
                            normal: {
                                show: true,
                                position: "inside",
                                formatter: '{c}%',
                                color: ' #3B4053',
                                fontSize: 16 * this.relativeRate,
                                rotate: 90
                            }, emphasis: {
                                show: false
                            }
                        },
                        data: assistData
                    }]
                };
                var ele=document.getElementById(element);
                ele.style.width=window.innerWidth+'px';
                var chart=this.echarts.init(ele);
                chart.setOption(option);
            },
            getNewList(list,list2){
                var newList=[];
                for(var i=0;i<list.length;i++){
                    if(parseFloat(list[i].proportion)>0){
                        list2.push({
                            name: list[i].name,
                            value: list[i].proportion,
                        });
                        var obj= {
                            name: list[i].name,
                            value: list[i].proportion,
                            itemStyle: {
                                normal: {
                                    color: new this.echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                        offset: 0,
                                        color: this.pieColor[list[i].index].start
                                    }, {
                                        offset: 1,
                                        color: this.pieColor[list[i].index].end
                                    }], false),
                                },
                            }
                        }
                        newList.push(obj);
                    }
                }
                return newList;
            },
            showCompany() {
                this.tempCompany = this.currentCompany;
                if (this.companys.length == 0) {
                    axios.get('../static/json/companylist.json').then(res => {
                        this.companys = res.data.companyList;
                    })
                }
                this.popupVisible = true;
            },
            confirmCompany: function () {
                this.popupVisible = false;
                this.currentCompany = this.tempCompany;
                this.drawRiseReason();
            },
            confirmYear: function () {
                this.popupYear = false;
                this.currentYear = this.tempYear;
                this.currentQuarter = this.tempQuarter;
                this.drawRiseReason();
            },
            showYear() {
                if (this.yearList.length == 0) {
                    for (var i = this.startYear; i <= this.endYear; i++) {
                        this.yearList.push(i + "年")
                    }
                }
                this.slots[0].values = this.yearList;
                this.popupYear = true;
            },
            btnSelect(company) {
                this.tempCompany = company
            },
            cancel() {
                this.popupVisible = false;
                this.popupYear = false;
            },
            onValuesChange(picker, values) {
                if (values[0]) {
                    this.tempYear = values[0].substring(0, 4);
                }
                var quarter = values[1].substring(1, 2);
                this.tempQuarter = (quarter == '一' ? 1 : quarter == '二' ? 2 : quarter == '三' ? 3 : 4);
            }
        }
    }
</script>

