        white-space: nowrap;
    }
    .table-fixed1>.even-odd:nth-child(even),.table-scroll2>.even-odd:nth-child(even){
        background: rgba(236,236,236,0.3);
    }
    .table-row1{
        display: table-row;
    }
    .table-column1{
        display: table-column;
        width: 2.4rem;
    }
    .table-row1>span{
        height: 1.1rem;
        display: table-cell;
        vertical-align: middle;
        border: 0.01rem solid;
    }
    /*底部选择框*/
    .time-dimension{
        margin: 0.4rem 0.15rem;
        height: 0.6rem;
        width: 1.5rem;

        padding: 0;
        box-sizing: border-box;
    }
    .time-dimension>>> .mint-tab-item-label {
        font-size: 0.28rem;
        color: #116DCF;
        letter-spacing: 0.04rem;
        border: 0.02rem solid #116DCF;
        border-radius: 1rem;
        line-height: 0.6rem;
        box-sizing: border-box;
        height: 0.6rem;
    }
    .mint-tabbar >.time-dimension.mint-tab-item.is-selected {
        background-color: transparent;
        color: #26a2ff;
    }
    .mint-tabbar > .mint-tab-item.is-selected >>> .mint-tab-item-label{
        background-image: linear-gradient(-180deg, #50A2FF 0%, #116DCF 100%);
        border-radius: 1rem;
        border: 0;
        color: #fff;
    }
    .company-show{
        display: inline-block;
        border: 1px solid #116DCF;
        border-radius: 0.3rem;
        width: 1.5rem;
        height: 0.5rem;
        line-height: 0.5rem;
        text-align: center;
        color: #116DCF;
        margin: 0.4rem 0 0.4rem 0.35rem;
    }
    .company-show:after{
        content: " ";
        display: inline-block;
        line-height: 0.113rem;
        height: 0.113rem;
        width: 0.113rem;
        border-bottom: 1px solid #116DCF;
        border-right: 1px solid #116dcf;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        transform: rotate(45deg);
        position: relative;
        top: -0.07rem;
        margin-left: 0.14rem;
    }
    .select-company{
        text-align: center;
        margin-top: 1rem;
        height:100%;
        overflow-y:scroll;
        -webkit-overflow-scrolling: touch;
    }
    .select-company>button{
        display: inline-block;
        background: transparent;
        float: left;
        height: 0.6rem;
        line-height: 0.6rem;
        width: 1.4rem;
        border-radius: 1rem;
        margin:0.05rem 0;
        border: 0.01rem solid transparent;
        box-sizing: border-box;
    }
    .select-company>button.selected{
        color: #116dfc;
        border: 0.01rem solid #116dfc
    }
    .select-company>button:nth-child(even){
        margin-right: 1.26rem;
        margin-left: 0.62rem;
    }
    .select-company>button:first-child{
        margin-right: 5.48rem;
        margin-left: 0.62rem;
        margin-top: 0.26rem;
    }
    .select-company>button:nth-child(2n+3){
        margin-right: 1.56rem;
        margin-left: 1.26rem;
    }
    .select-title{
        position: absolute;
        top: 0;
        left: 0;
        height: 1rem;
        right: 0;
        margin: 0 0.3rem;
        border-bottom: 1px solid #ECECEC;
        line-height: 1rem;
        text-align: center;
        font-size: 0.32rem;
        color: #3B4053;
        letter-spacing: 0;
        box-sizing: border-box;
        /* background: #fff;
         z-index: 1;*/
    }
    .select-title>.cancel{
        opacity: 0.7;
        font-size: 0.28rem;
        color: #3B4053;
        float: left;
    }
    .select-title>.confirm{
        opacity: 0.7;
        font-size: 0.28rem;
        color: #116DCF;
        float: right;
    }
    .blank-fill{
        display: inline-block;
        width: 100%;
        height: 1rem;
    }
</style>



